
:root{
    --menu-border-color:#4895EF;
    --background-color: #ffffeb;
    --text-color: black;
    --active:#d62828;
    --menu-text-color: #f72585;
    --menu-text-hover-color:#4cc9f0;
    --button-background-color: #ff80ba;
    --button-background-color-hover:#8dafff;
}
@media (prefers-color-scheme: dark){  /*si el usuario tiene  elmodo negro*/
    :root{
        --menu-border-color:#ebb2ff;
        --background-color: #051739;
        --text-color: white;
        --active:#f77f00;
        --menu-text-color: #ff6cf3;
        --menu-text-hover-color:#83d0ff;
        --button-background-color: #427bfe;
        --button-background-color-hover:#c865f9ff;
    }
}

/* function menuItem => menu.js */
.menu-item{
    padding: 10px;
}
.menu-item a{
    color: var(--menu-text-color);
    font-weight: 600;
    font-size: 18px;
    transition: all .1s ease-in;
    text-decoration: none;
}
.menu-item a:hover{
    color: var(--menu-text-hover-color);
}


/* stylos de function Menu() => menu.js */
.menu-wrapper{
    border-bottom: 1px solid var(--menu-border-color);
}
.menu{
    margin: 0;
    list-style: none;
    display: flex;
    padding: 0;
}

